body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes floatBoo {
  0% {left: 0px; bottom: -50px;}

  15% {left: -80px; bottom: -80px;}

  45% {left: 20px; bottom: -120px;}

  75% {left: 80px; bottom: -100px;}

  90%{left: 100px; bottom: -60px;}

  100% {left: 0px; bottom: -50px;}
}

@keyframes floatBoo {
  0% {left: 0px; bottom: -50px;}

  15% {left: -80px; bottom: -80px;}

  45% {left: 20px; bottom: -120px;}

  75% {left: 80px; bottom: -100px;}

  90%{left: 100px; bottom: -60px;}

  100% {left: 0px; bottom: -50px;}
}

@-webkit-keyframes floatShadow {
  0% {left: 0px; bottom: 0px; -webkit-transform: scale(1); transform: scale(1)}

  15% {left: -80px; bottom: 0px; -webkit-transform: scale(1.1); transform: scale(1.1)}

  45% {left: 20px; bottom: 0px; -webkit-transform: scale(1.3); transform: scale(1.3)}

  75% {left: 80px; bottom: 0px; -webkit-transform: scale(1.25); transform: scale(1.25)}

  90% {left: 100px; bottom: 0px; -webkit-transform: scale(1.05); transform: scale(1.05)}

  100% {left: 0px; bottom: 0px; -webkit-transform: scale(1); transform: scale(1)}
}

@keyframes floatShadow {
  0% {left: 0px; bottom: 0px; -webkit-transform: scale(1); transform: scale(1)}

  15% {left: -80px; bottom: 0px; -webkit-transform: scale(1.1); transform: scale(1.1)}

  45% {left: 20px; bottom: 0px; -webkit-transform: scale(1.3); transform: scale(1.3)}

  75% {left: 80px; bottom: 0px; -webkit-transform: scale(1.25); transform: scale(1.25)}

  90% {left: 100px; bottom: 0px; -webkit-transform: scale(1.05); transform: scale(1.05)}

  100% {left: 0px; bottom: 0px; -webkit-transform: scale(1); transform: scale(1)}
}

